import React from "react"
import styles from "./ResponsiveFrame.module.css"

export default function ResponsiveFrame({ src, title }) {
    return (
        <div className={styles.mapResponsive} onWheel={e => e.stopPropagation()}>
            <iframe
            title={title}
            src={src}
            frameBorder="false"
            allowFullScreen
            />
        </div>
    );
}
