import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/Layout"
import WhatsappLink from "../components/WhatsappLink"
import WeChatLink from "../components/WeChatLink"
import SEO from "../components/seo"
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Table from "react-bootstrap/Table";
import Badge from "react-bootstrap/Badge";
import Img from "gatsby-image";
import Price from "../components/Price";
import ResponsiveFrame from "../components/ResponsiveFrame";

export const query = graphql`
query {
  allFile(filter: {
    relativePath: { in: [ "box-beef-slices.jpg", "box-chicken-fish.jpg", "box-hong-shao-rou.jpg" ] }
  }) {
    edges {
      node {
        id,
        childImageSharp {
          fluid(quality: 90) {
            ...GatsbyImageSharpFluid,
          }
        }
      }
    }
  }
}
`;

const HeFanPage = ({ data }) => {
  const deliveryLocations = [
    { code: "A1", location: "Xior Student Housing - Residence Barbarasteeg" },
    { code: "A2", location: "Xior Student Housing - Residence Phoenixstraat" },
    { code: "B", location: "Jumbo Bastiaansplein 135" },
    { code: "C", location: "Professor Schermerhornstraat" },
    { code: "D", location: "Michiel de Ruyterweg" },
    { code: "E", location: "Cafe Labs" },
    { code: "F", location: "S.W.V. Plankenkoorts" },
    { code: "G", location: "Camelot" },
    { code: "H", location: "TU Delft - Industrial Design" },
    { code: "I", location: "SHS Delft Aan het Verlaat" },
    { code: "J", location: "Aries Foodie" },
  ]

  return (
    <Layout>
      <SEO title="HeFan" />
      <h1 className="display-1">Hé fàn <Price amount={6.25}/></h1>
      <p className="lead">About our complete lunch and dinner boxes, and how to get them.</p>

      <Row>
        {
          data.allFile.edges.map(({ node }) => {
            return (<Col key={node.id} md={4}>
              <Img sizes={{ ...node.childImageSharp.fluid, aspectRatio: 4 / 3 }} />
            </Col>);
          })
        }
      </Row>

      <p className="mt-3">
        We deliver authentic Chinese food in the area of Delft for reasonable prices.
        We do this by preparing food for lunch or dinner in large quantities, and then delivering it all at once.
        The actual dishes vary from day to day so that we can work with fresh in-season produce and ensure plenty of variety.
        We usually settle on a menu one day ahead and notify you through <WhatsappLink /> or <WeChatLink />.
      </p>

      <h2>Delivery</h2>

      <p>
        As soon as we finished preparing everyone's food we start the delivery.
        To ensure everyone gets their food as soon as possible we first deliver to the fixed locations listed below.
        We notify you about when we expect to be at your chosen pick-up location.
        After the majority of the meals have been delivered, we do door-to-door deliveries.
        We are always open to feedback on this process.
        If you have a wish, be sure to let us know!
      </p>
      <Row className="justify-content-center">
        <Col md={6}>
          <Table borderless size="sm">
            <thead>
              <tr>
                <th>Code</th>
                <th>Location</th>
              </tr>
            </thead>
            <tbody>
              {
                deliveryLocations.map(({ code, location }) => (
                  <tr key={code}>
                    <td className="text-monospace">{code}</td>
                    <td>{location}</td>
                  </tr>
                ))
              }
            </tbody>
          </Table>
        </Col>
        <Col md={6}>
          <ResponsiveFrame src="https://www.google.com/maps/d/embed?mid=12ljTJyz4qdFLZXvT3EEMuXGFmBVRXGSu" title="Aries Foodie Delivery Map" />
        </Col>
      </Row>

      <h2>How to order</h2>
      <p>
        As mentioned before, we send you the options for your hé fàn through <WhatsappLink /> or <WeChatLink/>.
        To place an order, you simply send a message with:
      </p>
      <ol>
        <li>The next order number</li>
        <li>Your order</li>
        <li>Your desired delivery location code</li>
      </ol>
      <p>
        For example, if the last order before you is 19 and you want options 1 and 2 delivered at the Cafe Labs location, you would send a message like <span style={{ fontSize: "120%" }}><Badge variant="info">20. 1+2 E</Badge></span>.
      </p>

      <h2>Payment</h2>
      <p>
        You can pay for your order by a simple bank transfer.
        To make things easier for return customers we sell 5x and 10x hé fàn coupons at a discount.
        We keep track of these coupons, which we call <em>times cards</em>, for you.
      </p>
    </Layout>
  )
}

export default HeFanPage
